
















var maxNum = 9, _self;
export default {
	data() {
		return {
			imgLists : [],
			btnImg : true
		}
	},
	onLoad:function(){
		_self = this;
	},
	methods:{
		addImg : function(){
			var num = maxNum - _self.imgLists.length;
			if(num < 1){return false;}
			uni.showLoading({
				title:"加载中..."
			});
			uni.chooseImage({
				count: num,
				sizeType: ['compressed'],
				success: function(res) {
					_self.imgLists = _self.imgLists.concat(res.tempFilePaths);
					if (_self.imgLists.length >= maxNum){
						_self.btnImg = false;
					}
					uni.hideLoading();
				}
			});
		},
		removeImg : function(e){
			var index = e.currentTarget.id.replace('grace-items-img-', '');
			_self.imgLists.splice(index, 1);
			_self.imgLists = _self.imgLists;
			if (_self.imgLists.length < maxNum) {
				_self.btnImg = true;
			}
		},
		showImgs : function(e){
			var currentImg = e.currentTarget.dataset.imgurl;
			uni.previewImage({
			  urls: this.imgLists,
			  current : currentImg
			})
		}
	}
}
